<template>
    <div id="mx_outLibrary">
        <a-skeleton
            :loading="loadingActive"
            active
            :paragraph="{rows: 20}"
        >
            <a-tabs
                :activeKey="activeKey"
                @change="callback"
            >
                <a-tab-pane
                    tab=""
                    key="1"
                >
                    <div v-if="!isRecipe && !isAddFoods && !isMailReval && !isSucceedSub && !isSucceedSubs && !isRetrievalGoods">
                        <div
                            class="mx_canteen"
                            style="display: flex;"
                        >
                            <div style="marginRight:20px;">
                                
                            </div>
                            <a-radio-group
                                v-model="canteenId"
                                style="width:94%;"
                                @change="onChange"
                            >
                                <a-radio
                                    v-for="(item,index) in canteen"
                                    :value="item.id"
                                >{{item.title}}</a-radio>
                            </a-radio-group>
                        </div>
                        <a-form
                            layout="inline"
                            style="margin:20px 0;"
                        >
                            <a-form-item label="">
                                <a-select
                                    :value="deflVal"
                                    @change="mxChangeVal"
                                    style="width: 120px;"
                                >
                                    <a-select-option
                                        v-for="item in outPurposeList"
                                        :value="item.id"
                                    >{{item.title}}</a-select-option>
                                </a-select>
                            </a-form-item>
                        </a-form>
                        <a-form
                            layout="inline"
                            style="margin:20px 0;display: flex;justify-content: space-between;align-items: center;"
                        >
                            <div>
                                <a-form-item label="">
                                    <a-date-picker
                                        :disabled=userTime
                                        :value="moment(userDate,'YYYY-MM-DD')"
                                        format="YYYY-MM-DD"
                                        showTime
                                        @change="onDateChange"
                                    />
                                </a-form-item>
                                <a-form-item style="marginLeft:56px;">
                                    <a-button
                                        style="marginRight:16px;background: #f2f2f2;color:#000;"
                                        @click="mxRecipe"
                                    ></a-button>
                                    <a-button
                                        style="background: #f2f2f2;color:#000;"
                                        @click="mxAddFoods"
                                    ></a-button>
                                </a-form-item>
                            </div>
                            <div>
                                <a-form-item>
                                    <a-button style="marginRight:16px;background: #f2f2f2;color:#000;"></a-button>
                                    <a-button style="marginRight:16px;background: #f2f2f2;color:#000;"></a-button>
                                    <a-button
                                        type="primary"
                                        @click="mxOutRetrieval"
                                    ></a-button>
                                </a-form-item>
                            </div>
                        </a-form>
                        <!--  -->
                        <a-skeleton
                            :loading="loading"
                            active
                            :paragraph="{rows: 20}"
                        >
                            <a-table
                                :columns="columns"
                                :dataSource="data"
                                bordered
                                :pagination="false"
                                :footer="footer"
                                v-if="data.length>0"
                            >
                                <div
                                    style="display: flex;justify-content: space-around;"
                                    slot="material_single_src"
                                    slot-scope="text, record,index"
                                >
                                    <viewer>
                                        <img
                                            style="width:60px;heigth:60px"
                                            :src="text"
                                        >
                                    </viewer>
                                </div>
                                <div
                                    style="display: flex;justify-content: space-around;"
                                    slot="num"
                                    slot-scope="text, record,index"
                                >
                                    <div style="position: relative;">
                                        <!-- @change="mxInpVal" -->
                                        <a-input
                                            @change="e=>mxInpVal(e,record)"
                                            :data-index="index"
                                            :value='text'
                                            style="width:68px;paddingRight:24px;"
                                        />
                                        <!-- v-model="inputVal" -->
                                        <span style="position: absolute;right:6px;lineHeight:32px;"></span>
                                    </div>
                                </div>
                                <template
                                    slot="operation"
                                    slot-scope="text, record, index"
                                >
                                    <div class="editable-row-operations">
                                        <div>
                                            <a
                                                class="mx_updata mx_color"
                                                @click="mxDel(record.id)"
                                            ></a>
                                            <a-modal
                                                title=""
                                                centered
                                                :maskStyle="{'opacity':'0.1','background':'#000','animation':'none'}"
                                                v-model="isDel"
                                                @cancel="mxCancel"
                                                @ok="mxDelete"
                                                okText=""
                                                cancelText=""
                                            >
                                                <div class="mx_model">
                                                    <div class="mx_model_mainDel">
                                                        
                                                    </div>
                                                </div>
                                            </a-modal>
                                        </div>
                                    </div>
                                </template>
                            </a-table>
                            <!--  -->
                            <a-table
                                :columns="columnNull"
                                :dataSource="dataNull"
                                bordered
                                :pagination="false"
                                :footer="footerNull"
                                v-else
                            />
                        </a-skeleton>
                    </div>
                    <!--  -->
                    <div
                        class="isRecipe"
                        v-if="isRecipe"
                    >
                        <!--  -->
                        <a-breadcrumb separator=" ">
                            <a-breadcrumb-item>
                                <a-icon
                                    @click="backRecipe"
                                    class="mx_header_icon"
                                    type="arrow-left"
                                />
                            </a-breadcrumb-item>
                            <a-breadcrumb-item class="mx_header_text"></a-breadcrumb-item>
                        </a-breadcrumb>
                        <div style="display: flex;justify-content: space-between;align-items: center;margin:26px 0;">
                            <div style="display: flex;align-items: center;">
                                <div style="display: flex;align-items: center;">
                                    <div class="mx_dateList_name">{{nowdata}}&nbsp;&nbsp;{{weeks}}</div>
                                </div>
                                <div class="mx_dateList_right">
                                    <span
                                        class="mx_mouth"
                                        @click="mxPreDay"
                                    >
                                        <a-icon type="left" /></span>
                                    <span
                                        class="mx_thismouth"
                                        @click="mxDay"
                                    ></span>
                                    <span
                                        class="mx_mouth"
                                        @click="mxNextDay"
                                    >
                                        <a-icon type="right" /></span>
                                </div>
                            </div>
                            <a-button
                                type="primary"
                                @click="mxPromptlyCreate"
                            ></a-button>
                        </div>
                        <!--  -->
                        <a-skeleton
                            :loading="loadingRecipes"
                            :paragraph="{rows: 20}"
                        >
                            <a-table
                                :columns="columnMeal"
                                :dataSource="dataMeal"
                                bordered
                                :footer="footerMeal"
                                :pagination="false"
                            >
                                <template
                                    v-for="(col,index) in ['breakfast', 'lunch', 'dinner', 'snack']"
                                    :slot="col"
                                >
                                    <div
                                        :key="index"
                                        style="position:relative"
                                    >
                                        <p>{{titleDate[index]}}</p>
                                        <!-- 1234 -->
                                        <a-radio
                                            :value='index'
                                            :checked="mxcheck == index?true:false"
                                            @click="mxChecked(index)"
                                            style="position: absolute;top:0;right:0"
                                        />
                                    </div>
                                </template>
                                <div
                                    slot="morning"
                                    slot-scope="text, record,index"
                                >
                                    <p v-for="item in text">{{item.title}}</p>
                                </div>
                                <div
                                    slot="noon"
                                    slot-scope="text, record,index"
                                >
                                    <p v-for="item in text">{{item.title}}</p>
                                </div>
                                <div
                                    slot="night"
                                    slot-scope="text, record,index"
                                >
                                    <p v-for="item in text">{{item.title}}</p>
                                </div>
                                <div
                                    slot="add_meals"
                                    slot-scope="text, record,index"
                                >
                                    <p v-for="item in text">{{item.title}}</p>
                                </div>
                            </a-table>
                        </a-skeleton>
                    </div>
                    <!--  -->
                    <div v-if="isAddFoods">
                        <!--  -->
                        <a-breadcrumb separator=" ">
                            <a-breadcrumb-item>
                                <a-icon
                                    @click="backAddFoods"
                                    class="mx_header_icon"
                                    type="arrow-left"
                                />
                            </a-breadcrumb-item>
                            <a-breadcrumb-item class="mx_header_text"></a-breadcrumb-item>
                        </a-breadcrumb>
                        <!--  -->
                        <div class="components-input-demo-presuffix">
                            <p class="hmTxt">:</p>
                            <a-input
                                placeholder="/"
                                class="seachBox"
                                style="width:200px;"
                                v-model="mxClassVal"
                                ref="userNameInput"
                            >
                                <a-icon
                                    v-if="mxClassVal"
                                    theme="filled"
                                    slot="suffix"
                                    type="close-circle"
                                    @click="emitEmpty"
                                    style="color:#999"
                                />
                            </a-input>
                            <a-button
                                type="primary"
                                class="seachBtn"
                                @click="mxSeach"
                            ></a-button>
                        </div>
                        <div
                            v-if="addFoodList.length>0"
                            class="mx_foodsMain"
                        >
                            <div
                                class="mx_foodsList"
                                v-for="(item,index) in addFoodList"
                            >
                                <div style="display: flex;justify-content: space-between;align-items: center;marginBottom:10px;">
                                    <div>
                                        <span>{{item.storehou_title}}</span>
                                        <span style="marginLeft:10px;">{{item.batch_num}}</span>
                                    </div>
                                    <div>{{item.supplier_title}}</div>
                                </div>
                                <div style="display: flex;align-items: center;">
                                    <img
                                        style="width:77px;height:77px;marginRight:22px;"
                                        :src="item.material_single_src"
                                        alt=""
                                    >
                                    <div>
                                        <p>{{item.material_title}}</p>
                                        <p style="margin:0;">
                                            <span style="marginRight:40px;">&yen;{{item.order_unit_price}}</span>
                                            <span style="marginRight:40px;">{{item.order_num}}</span>
                                            <span>&yen;{{item.total_price}}</span>
                                        </p>
                                    </div>
                                </div>
                                <p style="marginTop:20px;">{{item.yj_str}}</p>
                                <p>{{item.record_time_str}}</p>
                                <div style="display: flex;justify-content: space-between;align-items: center;">
                                    <p style="margin:0;">  {{item.shelf_life}}</p>
                                    <div>
                                        <!-- v-if="!isOverdue && !isRetrieval" -->
                                        <a-button
                                            v-if="!item.isAdd"
                                            type="primary"
                                            @click="()=>{item.isAdd = !item.isAdd}"
                                        ></a-button>
                                        <!-- @click="mxRetrieval" -->
                                        <div
                                            v-if="item.isAdd"
                                            style="display: flex;justify-content: space-between;align-items: center;"
                                        >
                                            <div style="position: relative;">
                                                <a-input
                                                    placeholder=""
                                                    style="width:108px; paddingRight:26px;"
                                                    :data-index="index"
                                                    v-model="item.inputVal"
                                                    @input="e=>mxNumVal(e,item.order_num)"
                                                />
                                                <!-- @keyup="value=value.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3')" -->
                                                <!-- @keyup="value=value.replace(/[^\d^\.]+/g,'').replace('.','$#$').replace(/\./g,'').replace('$#$','.')" -->
                                                <span style="position: absolute;right:7px;lineHeight:32px;"></span>
                                            </div>
                                            <a-button
                                                type="primary"
                                                style="marginLeft:10px;"
                                                @click="mxSureReval(item)"
                                            ></a-button>
                                            <!-- @click="()=>{item.isAdd = !item.isAdd}" -->
                                        </div>
                                        <!-- <p
                                        v-if="isOverdue"
                                        style="color:red;"
                                    ></p> -->
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div
                            v-else
                            style="width:100%;height:500px;display: flex;justify-content: center;align-items: center;"
                        >
                            <p v-if="isSearch"></p>
                            <p v-if="!isSearch"></p>
                        </div>
                    </div>
                    <!--  -->
                    <div v-if="isMailReval">
                        <!--  -->
                        <a-breadcrumb separator=" ">
                            <a-breadcrumb-item>
                                <a-icon
                                    @click="backMailReval"
                                    class="mx_header_icon"
                                    type="arrow-left"
                                />
                            </a-breadcrumb-item>
                            <a-breadcrumb-item class="mx_header_text">{{mealTime}}</a-breadcrumb-item>
                        </a-breadcrumb>
                        <a-form
                            :form="myForm"
                            @submit="secondSubmit"
                        >
                            <a-row class="mx_main_form">
                                <a-col :span="8">
                                    <div v-if="!isMoveLibrary">
                                        <a-form-item
                                            v-if="!isFrmLoss && !isOther"
                                            label=""
                                        >
                                            <textarea
                                                style="width:340px;height:96px;resize:none;marginRight:100px;lineHeight:20px;"
                                                v-model="remorkInfo"
                                                cols="30"
                                                rows="10"
                                                placeholder=""
                                            ></textarea>
                                        </a-form-item>
                                        <a-form-item
                                            v-if="isFrmLoss"
                                            label=""
                                        >
                                            <a-select
                                                :value="lossesId"
                                                style="width:300px;"
                                                @change="mxChangeLosses"
                                            >
                                                <a-select-option
                                                    v-for="item in lossesCauseList"
                                                    :value="item.id"
                                                >{{item.title}}</a-select-option>
                                            </a-select>
                                        </a-form-item>
                                        <a-form-item
                                            v-if="isFrmLoss"
                                            label=""
                                        >
                                            <textarea
                                                style="width:340px;height:96px;resize:none;marginRight:100px;lineHeight:20px;"
                                                v-model="remorkInfos"
                                                cols="30"
                                                rows="10"
                                                placeholder=""
                                            ></textarea>
                                        </a-form-item>
                                        <a-form-item
                                            v-if="isOther"
                                            label=""
                                        >
                                            <textarea
                                                style="width:340px;height:96px;resize:none;marginRight:100px;lineHeight:20px;"
                                                cols="30"
                                                rows="10"
                                                placeholder=""
                                                v-decorator="[
                                                '',
                                                {rules: [{ required: true, message: '' }]}
                                            ]"
                                            ></textarea>
                                        </a-form-item>
                                        <a-form-item
                                            v-if="!isFrmLoss || isOther"
                                            style="marginTop:248px;"
                                        >
                                            <a-button
                                                type="primary"
                                                html-type="submit"
                                            ></a-button>
                                        </a-form-item>
                                        <a-form-item
                                            v-if="isFrmLoss"
                                            style="marginTop:180px;"
                                        >
                                            <a-button
                                                type="primary"
                                                html-type="submit"
                                            ></a-button>
                                        </a-form-item>
                                    </div>
                                    <div v-if="isMoveLibrary">
                                        <a-form-item label="">
                                            <a-select
                                                :value="moveCanteen"
                                                style="width:300px;"
                                                @change="mxMoveCanteenChange"
                                            >
                                                <a-select-option
                                                    v-for="(item,index) in canteen"
                                                    :value="item.id"
                                                >{{item.title}}</a-select-option>
                                            </a-select>
                                        </a-form-item>
                                        <a-form-item label="">
                                            <a-select
                                                placeholder=""
                                                style="width:300px;"
                                                :value='storehouseId'
                                                @change="mxDamageChange"
                                            >
                                                <!-- v-decorator="[
                                                '',
                                                {rules: [{ required: true, message: '' }]}
                                            ]" -->
                                                <a-select-option
                                                    v-for="item in storehouseList"
                                                    :value="item.id"
                                                >{{item.title}}</a-select-option>
                                            </a-select>
                                        </a-form-item>
                                        <a-form-item label="">
                                            <textarea
                                                style="width:340px;height:96px;resize:none;marginRight:100px;lineHeight:20px;"
                                                cols="30"
                                                rows="10"
                                                placeholder=""
                                                v-model="removeRemork"
                                            ></textarea>
                                        </a-form-item>
                                        <a-form-item style="marginTop:80px;">
                                            <a-button
                                                type="primary"
                                                html-type="submit"
                                            ></a-button>
                                        </a-form-item>
                                    </div>
                                </a-col>
                                <a-col :span="16">
                                    <a-form-item
                                        v-if="!isFrmLoss || isOther"
                                        label=""
                                    >
                                        <div>
                                            <div class="clearfix">
                                                <a-upload
                                                    action="http://bate.ccb.micmio.com/api/common/upload"
                                                    listType="picture-card"
                                                    :fileList="fileList"
                                                    @preview="handlePreview"
                                                    @change="handleChange"
                                                >
                                                    <div v-if="fileList.length < 2">
                                                        <a-icon type="plus" />
                                                    </div>
                                                </a-upload>
                                                <a-modal
                                                    :visible="previewVisible"
                                                    :footer="null"
                                                    @cancel="handleCancel"
                                                >
                                                    <img
                                                        alt="example"
                                                        style="width: 100%"
                                                        :src="previewImage"
                                                    />
                                                </a-modal>
                                            </div>
                                            <a-upload
                                                action="http://bate.ccb.micmio.com/api/common/upload"
                                                :fileList="fileList"
                                                @preview="handlePreview"
                                                @change="handleChange"
                                            >
                                                <a-button>
                                                    <a-icon type="upload" />
                                                </a-button>
                                            </a-upload>
                                        </div>
                                    </a-form-item>
                                    <div v-if="!isMoveLibrary">
                                        <a-form-item
                                            v-if="!isFrmLoss || isOther"
                                            label=""
                                        >
                                            <div>
                                                <div class="clearfix">
                                                    <a-upload
                                                        action="http://bate.ccb.micmio.com/api/common/upload"
                                                        listType="picture-card"
                                                        :fileList="sedList"
                                                        @preview="handlePreviews"
                                                        @change="handleChanges"
                                                    >
                                                        <div v-if="sedList.length < 2">
                                                            <a-icon type="plus" />
                                                        </div>
                                                    </a-upload>
                                                    <a-modal
                                                        :visible="previewVisibles"
                                                        :footer="null"
                                                        @cancel="handleCancels"
                                                    >
                                                        <img
                                                            alt="example"
                                                            style="width: 100%"
                                                            :src="previewImages"
                                                        />
                                                    </a-modal>
                                                </div>
                                                <a-upload
                                                    action="http://bate.ccb.micmio.com/api/common/upload"
                                                    :fileList="sedList"
                                                    @preview="handlePreviews"
                                                    @change="handleChanges"
                                                >
                                                    <a-button>
                                                        <a-icon type="upload" />
                                                    </a-button>
                                                </a-upload>
                                            </div>
                                        </a-form-item>
                                    </div>
                                    <a-form-item
                                        v-if="isFrmLoss"
                                        label=""
                                    >
                                        <div>
                                            <div class="clearfix">
                                                <a-upload
                                                    action="http://bate.ccb.micmio.com/api/common/upload"
                                                    listType="picture-card"
                                                    :fileList="damagedList"
                                                    @preview="damagedPreview"
                                                    @change="damagedChange"
                                                >
                                                    <div v-if="damagedList.length < 2">
                                                        <a-icon type="plus" />
                                                    </div>
                                                </a-upload>
                                                <a-modal
                                                    :visible="damagedVisible"
                                                    :footer="null"
                                                    @cancel="damagedCancel"
                                                >
                                                    <img
                                                        alt="example"
                                                        style="width: 100%"
                                                        :src="damagedImage"
                                                    />
                                                </a-modal>
                                            </div>
                                            <a-upload
                                                action="http://bate.ccb.micmio.com/api/common/upload"
                                                :fileList="damagedList"
                                                @preview="damagedPreview"
                                                @change="damagedChange"
                                            >
                                                <a-button>
                                                    <a-icon type="upload" />
                                                </a-button>
                                            </a-upload>
                                        </div>
                                    </a-form-item>
                                </a-col>
                            </a-row>
                        </a-form>
                    </div>
                    <!--  -->
                    <div v-if="isRetrievalGoods">
                        <!--  -->
                        <a-breadcrumb separator=" ">
                            <a-breadcrumb-item>
                                <a-icon
                                    @click="backGoods"
                                    class="mx_header_icon"
                                    type="arrow-left"
                                />
                            </a-breadcrumb-item>
                            <a-breadcrumb-item class="mx_header_text"></a-breadcrumb-item>
                        </a-breadcrumb>
                        <!--  -->
                        <a-table
                            :columns="columnReturnGoods"
                            :dataSource="dreturnGoodsList"
                            bordered
                            :pagination="false"
                            style="margin:35px 0 90px 0;"
                        >
                            <!-- <template
                                slot="title"
                                slot-scope="currentPageData"
                                style="background: rgb(245, 245, 245);"
                            >
                                <div>
                                    <p style="margin:0;"><span style='margin: 0 0 0 20px;font-size:16px;font-weight: bold;'>&yen;{{sumPrice}}</span></p>
                                </div>
                            </template> -->
                            <template
                                slot="footer"
                                slot-scope="currentPageData"
                                style="position: relative;"
                            >
                                <div style="width:100%;height:200px;position: absolute;top:0;left:0;background: #fff;padding:22px;display: flex;align-items: center;">
                                    <div style="marginRight:140px;">

                                        <textarea
                                            style="width:390px;height:100px;resize:none;borderRadius: 5px;"
                                            placeholder=""
                                            v-model="backFoodsRemork"
                                            cols="30"
                                            rows="10"
                                        ></textarea>
                                    </div>
                                    <div>
                                        <div class="clearfix">
                                            <a-upload
                                                action="http://bate.ccb.micmio.com/api/common/upload"
                                                listType="picture-card"
                                                :fileList="tabsList"
                                                @preview="tabsPreview"
                                                @change="tabsChange"
                                            >
                                                <div v-if="tabsList.length < 3">
                                                    <a-icon type="plus" />
                                                </div>
                                            </a-upload>
                                            <a-modal
                                                :visible="tabsVisible"
                                                :footer="null"
                                                @cancel="tabsCancel"
                                            >
                                                <img
                                                    alt="example"
                                                    style="width: 100%"
                                                    :src="tabsImage"
                                                />
                                            </a-modal>
                                        </div>
                                        <a-upload
                                            action="http://bate.ccb.micmio.com/api/common/upload"
                                            :fileList="tabsList"
                                            @preview="tabsPreview"
                                            @change="tabsChange"
                                        >
                                            <!-- v-decorator="[
                                                    '',
                                                    {rules: [{ required: true, message: '' }]}
                                                ]" -->
                                            <a-button>
                                                <a-icon type="upload" />
                                            </a-button>
                                        </a-upload>
                                    </div>
                                </div>
                                <div style="width:100%;marginTop:200px;">
                                    <p style='text-align: right;margin-bottom: 0;'><span style='font-size:18px;color:red;'>&yen;{{totalPrice}}</span></p>
                                </div>
                            </template>
                            <div
                                style="display: flex;justify-content: space-around;"
                                slot="material_single_src"
                                slot-scope="text, record,index"
                            >
                                <img
                                    style="width:60px;heigth:60px"
