        },
        // 
        dateWeek(m) {
            let date = moment().subtract(m, "days")
            this.nowdata = moment(date).format('YYYYMMDD')
            let num = moment(date).format('d');
            if (num == 1) {
                this.weeks = ""
            } else if (num == 2) {
                this.weeks = ""
            } else if (num == 3) {
                this.weeks = ""
            } else if (num == 4) {
                this.weeks = ""
            } else if (num == 5) {
                this.weeks = ""
            } else if (num == 6) {
                this.weeks = ""
            } else {
                this.weeks = ""
            }
            this.timeDate = moment(this.nowdata, 'YYYY-MM-DD').format('x') / 1000;
            this.loadingRecipes = true;
            this.getRecipesList(this.canteenId, this.timeDate);
