                    }
                })
            } else {
                this.$message.warning('');
            }
        },
        // 
        mxOutRetrieval() {
            this.outPurposeList.forEach(item => {
                if (item.id == this.deflVal) {
                    this.mealTime = item.title;
                    this.scene = item.scene;
                }
            })
            if (this.deflVal == "96") { this.mealTime = ''; this.isMailReval = true; }
            else if (this.deflVal == "97") { this.mealTime = ''; this.isMailReval = true; }
            else if (this.deflVal == "98") { this.mealTime = ''; this.isMailReval = true; }
            else if (this.deflVal == "99") { this.mealTime = ''; this.isMailReval = true; }
            else if (this.deflVal == "102") { this.mealTime = ''; this.isMailReval = true; this.isFrmLoss = true; this.getLossesCause(); }
            else if (this.deflVal == "0") { this.mealTime = ''; this.isMailReval = true; this.isOther = true; }
            else if (this.deflVal == "103") { this.isRetrievalGoods = true; this.dreturnGoodsList = this.data; }
            else { this.mealTime = ''; this.isMailReval = true; this.isMoveLibrary = true; this.moveCanteen = this.canteen[0].id; this.getStorehouseList(this.canteen[0].id); }
        },
        // 
        mxAddFoods() {
