                this.isSearch = true;
            }
            this.$get("api/school/searchmaterial?keyword=" + this.mxClassVal + '&canteen_id=' + this.canteenId).then(res => {
                if (res.data.code == 0) {
                    if (res.data.data.length <= 0) {
                        this.isResult = false;
                        this.isSearch = false;
                    }
                    res.data.data.forEach(item => {
